import { createStore } from "@stencil/store";
import { ProductCardData } from "./listing";

export const recipe = createStore({} as RecipeData);

export interface RecipeData {
    title: string;
    summary: string;
    image: string;
    time: string;
    yield: string;

    category: string;
    categoryLink: string;
    cuisine: string;
    cuisineLink: string;

    nutrition: RecipeNutritionData;
    ingredients: RecipeIngredientData[];
    preparation: RecipePreparationSectionData[];

    suggestions: ProductCardData[];
}

export interface RecipeNutritionData {
    calories: string;
    fats: string;
    saturatedFats: string;
    sugars: string;
    salt: string;
    protein: string;
    carbohydrates: string;
}


export interface RecipeIngredientData {
    name: string;
    amount: string;
}

export interface RecipePreparationSectionData {
    name: string;
    steps: string[];
}