import { Component, h, Prop } from '@stencil/core';
import { recipe, RecipeData } from "../../../global/data/recipe";
import { DataLayer } from '../../../global/dataLayer';

declare global { interface Window { dataLayer: any[] }}

@Component({
    tag: 'ks-page-recipe',
    styleUrl: 'recipe.css'
})
export class PageRecipe{
    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() recipeData: string;

    componentWillLoad() {
        const recipeDataElement = document.getElementById(this.recipeData);
		const recipeData = JSON.parse(recipeDataElement.innerHTML) as RecipeData;

        Object.keys(recipeData).map(key => {
			recipe.set(key as keyof RecipeData, recipeData[key]);
		});

        this.track();
    }

    track() {
        DataLayer.recipe(recipe.state);
    }

	render() {
        const nutrition = recipe.get('nutrition');

		return <ks-page-base skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            <ks-container>
                <div class="recipe-container">
                    <ks-recipe-info
                        image={recipe.get('image')}
                        time={recipe.get('time')}
                        yield={recipe.get('yield')}

                        cuisine={recipe.get('cuisine')}
                        cuisine-link={recipe.get('cuisineLink')}

                        category={recipe.get('category')}
                        category-link={recipe.get('categoryLink')}
                    >
                        <h1 slot="heading">{recipe.get('title')}</h1>
                        <p slot="summary">{recipe.get('summary')}</p>

                        <ks-recipe-nutrition type='kalorie' value={nutrition.calories} slot='nutrition'></ks-recipe-nutrition>
                        <ks-recipe-nutrition type='tłuszcze' value={nutrition.fats} slot='nutrition'></ks-recipe-nutrition>
                        <ks-recipe-nutrition type='nasycone' value={nutrition.saturatedFats} slot='nutrition'></ks-recipe-nutrition>
                        <ks-recipe-nutrition type='cukry' value={nutrition.sugars} slot='nutrition'></ks-recipe-nutrition>
                        <ks-recipe-nutrition type='sól' value={nutrition.salt} slot='nutrition'></ks-recipe-nutrition>
                        <ks-recipe-nutrition type='białka' value={nutrition.protein} slot='nutrition'></ks-recipe-nutrition>
                        <ks-recipe-nutrition type='węglowodany' value={nutrition.carbohydrates} slot='nutrition'></ks-recipe-nutrition>

                    </ks-recipe-info>
                    <ks-recipe-procedure>
                        {recipe?.get("ingredients")?.map(ingredient => 
                            <ks-recipe-ingredient name={ingredient.name} amount={ingredient.amount} slot='ingredients'></ks-recipe-ingredient>    
                        )}

                        {recipe?.get("preparation")?.map(section => [
                            <h2 slot='preparation'>{section.name}</h2>,
                            <p slot='preparation'>
                                {section?.steps?.join(' ')}
                            </p>
                        ])}
                    </ks-recipe-procedure>
                </div>
            </ks-container>

            {recipe?.get('suggestions')?.length > 0 ? [
                <h3 class="suggested-heading">Produkty Polecane</h3>,
                <ks-product-container>
                    {recipe?.get('suggestions')?.map(card =>
                        <ks-product-card product={card}/>
                    )}

                </ks-product-container>
            ] : null}

        </ks-page-base>;
	}
}
